<template>
   <div id="container" class="card mb-3">
  	<img v-if="img_status" class="card-img-top" v-bind:src="imgsrc" alt="Card image cap">
  	<div class="card-body">
    		<h5 class="card-title">{{ title }}</h5>
    		<p class="card-text">{{ content }}</p>
    		<p class="card-text"><a v-if="link1_status" v-bind:href="link1"><span class="fab fa-github"></span></a></p>
	</div>
   </div> 
</template>

<script>
export default {
  name: 'Project',
  props: ['title', 'content', 'link1', 'imgsrc'],
  data() {
	return {
		link1_status: false,
		img_status: false
	}
  },
  mounted: function() {
	if (this.link1 != "None") {
		this.link1_status = true;
	}
	if (this.imgsrc != "None") {
		this.img_status = true;
	}
  }
}
</script>

<style scoped>
	img {
		max-height: 400px;
		max-width: 300px;
		margin-left: auto;
		margin-right: auto;
		display: block;
	}
	#container {
		margin: 20px 50px;
	}
	span {
		font-size: 40px;
	}
</style>



